import React from "react";
import { Button, Col, Row } from "react-bootstrap";
import { imgDerian } from "../../assets";
import "./Welcome.scss";

const Welcome = () => {
  return (
    <Row className="welcome-page">
      <Col
        md={4}
        className="d-flex justify-content-center justify-content-lg-start"
      >
        <img src={imgDerian} alt="avatar" />
      </Col>
      <Col>
        <h4>Hi, I am</h4>
        <h1>Derian Indra Pratama</h1>
        <h3>Web Developer</h3>
        <p>
          I am a person who has passion in Information Technology, especially in
          programming. I have an enthusiasm with something that concerned with
          the Web Programming.
        </p>
        <Button>Download CV</Button>
      </Col>
    </Row>
  );
};

export default Welcome;
