import React from "react";
import { Col, Image, Row, Tab, Tabs } from "react-bootstrap";
import { HeaderContent } from "../../components";
import { imgNpks } from "../../assets";
import "./Portfolio.scss";

const Portfolio = () => {
  return (
    <Row className="portfolio-wrapper">
      <Col md={{ span: 10, offset: 1 }}>
        <HeaderContent title="My Portfolio" />
        <Row>
          <Col className="text-center mb-3">
            <Tabs variant="pills" className="d-flex justify-content-center">
              <Tab
                eventKey="All"
                title="All Projects"
                tabClassName="mb-5 tab-text"
              >
                <Row className="content">
                  <Col xs={6} md={4} className="mb-3">
                    <Image src={imgNpks} alt="npks" fluid className="thumb" />
                    <a href="#"></a>
                  </Col>
                  <Col xs={6} md={4} className="mb-3">
                    <Image src={imgNpks} alt="npks" fluid className="thumb" />
                    <a href="#"></a>
                  </Col>
                  <Col xs={6} md={4} className="mb-3">
                    <Image src={imgNpks} alt="npks" fluid className="thumb" />
                    <a href="#"></a>
                  </Col>
                </Row>
              </Tab>
              <Tab eventKey="web" title="Web" tabClassName="mb-5 tab-text">
                <Row className="content">
                  <Col xs={6} md={4} className="mb-3">
                    <Image src={imgNpks} alt="npks" fluid className="thumb" />
                    <a href="#"></a>
                  </Col>
                  <Col xs={6} md={4} className="mb-3">
                    <Image src={imgNpks} alt="npks" fluid className="thumb" />
                    <a href="#"></a>
                  </Col>
                  <Col xs={6} md={4} className="mb-3">
                    <Image src={imgNpks} alt="npks" fluid className="thumb" />
                    <a href="#"></a>
                  </Col>
                </Row>
              </Tab>
              <Tab
                eventKey="mobile"
                title="Mobile"
                tabClassName="mb-5 tab-text"
              >
                <Row className="content">
                  <Col xs={6} md={4} className="mb-3">
                    <Image src={imgNpks} alt="npks" fluid className="thumb" />
                    <a href="#"></a>
                  </Col>
                </Row>
              </Tab>
            </Tabs>
          </Col>
        </Row>
      </Col>
    </Row>
  );
};

export default Portfolio;
